<template>
  <div class="body-container" :class="{'center-flex': $attrs.cards.length === 0}">
    <div class="list-bar" v-if="$attrs.cards.length != 0">
      <customcheckbox v-model="selectAllBox" @input="itemToSelected"></customcheckbox>
      <div class="labels" v-if="getSelected === 0">
        <div class="label">Status</div>
        <div class="label">Date Created</div>
        <div class="label">Last Edited</div>
        <div class="label">Created By</div>
      </div>
      <div class="selected-label" v-if="getSelected != 0">{{ getSelected }} Selected</div>
    </div>
    <div class="no-contents" v-if="$attrs.cards.length === 0">No projects currently</div>
    <div class="list-padding" v-if="$attrs.cards.length != 0">
      <ListItem class="list-item" v-for="card in $attrs.cards" :key="card.id" :details="card"></ListItem>
    </div>
  </div>
</template>

<script>
import ListItem from "@/components/ListItem.vue";
import { mapGetters, mapMutations } from "vuex";
import customcheckbox from "@/components/Input/Checkbox.vue";
export default {
  data() {
    return {
      selectAllBox: false,
      selected: []
    };
  },
  components: { ListItem, customcheckbox },
  computed: {
    ...mapGetters(["getSelected"])
  },
  methods: {
    ...mapMutations(["setSelectedOfAll"]),
    itemToSelected(payload) {
      this.selected.push(payload);
    }
  }
};
</script>

<style lang="scss" scoped>
.body-container {
  background-color: $mercury-20;
  .list-bar {
    height: 50px;
    background-color: $mercury-10;
    border-bottom: solid 1px $mercury-40;
    display: grid;
    grid-template-columns: 70px auto;
    align-items: center;
    .labels {
      display: flex;
    }
    .label {
      font-family: Heebo;
      font-size: 11.5px;
      font-weight: normal;
      font-stretch: normal;
      font-style: normal;
      line-height: normal;
      letter-spacing: 0.61px;
      color: $mercury-80;
      text-transform: uppercase;
    }
    .selected-label {
      font-family: Heebo;
      font-size: 12.5px;
      font-weight: normal;
      font-stretch: normal;
      font-style: normal;
      line-height: normal;
      letter-spacing: normal;
      color: $outer-space-30;
    }
  }
  .list-padding {
    padding: 16px 23px;
    .list-item {
      margin-bottom: 10px;
    }
  }
}
.center-flex {
  display: flex;
  align-items: center;
  justify-content: center;
}
.no-contents {
  font-family: Heebo;
  font-size: 20px;
  font-weight: 800;
  font-stretch: normal;
  font-style: normal;
  line-height: 1.3;
  letter-spacing: normal;
  color: $mercury-50;
}
</style>