<template>
  <div class="dash">
    <section id="header">
      <div class="top">
        <h1>Hi {{ $store.state.user.username }}!</h1>
        <img src="@/assets/avatar.png" width="33px" height="33px" />
      </div>
      <div class="h-line"></div>
      <div class="bottom" v-if="showwelcome">
        <p>Welcome Message</p>
        <img src="@/assets/icon/cross.svg" @click="showwelcome = false" />
      </div>
      <div class="h-line" v-if="showwelcome"></div>
    </section>
    <section id="body">
      <div class="projects">
        <div class="heading">
          <h1>Recent Projects</h1>
          <buttonsuccess @clicked="$router.push('/projects')">View all projects</buttonsuccess>
        </div>
        <div class="cards">
          <card
            v-for="(card, i) in getCards.slice(0,4)"
            :key="i"
            :class="'card-' + i"
            :index="i"
            :details="card"
            @duplicate="openDuplicateModal"
            @del="openDelModal"
          ></card>
          <div class="card card-1" v-if="getCards.slice(0,4).length < 2"></div>
          <div class="card card-2" v-if="getCards.slice(0,4).length < 3"></div>
          <div class="card card-3" v-if="getCards.slice(0,4).length < 4"></div>
        </div>
      </div>
      <div class="activity">
        <div class="heading">
          <h1>Recent Activity</h1>
        </div>
        <div class="activity-list">
          <div v-for="(work,i) in getActivity" :key="i" class="list-item">
            <h4>{{work.date}}</h4>
            <div class="v-line"></div>
            <p>{{work.user}}</p>
          </div>
        </div>
      </div>
    </section>
    <DuplicateModal
      v-if="duplicate.show"
      :cardname="duplicate.title"
      @close="duplicate.show = false"
      @duplicate="duplicateProject"
    ></DuplicateModal>
    <DeleteModal
      v-if="del.show"
      :cardname="del.title"
      @close="del.show = false"
      @delete="deleteProject"
    ></DeleteModal>
  </div>
</template>

<script>
import { mapGetters, mapMutations } from "vuex";
import card from "@/components/Card.vue";
import buttonsuccess from "@/components/ButtonSuccess.vue";
import DuplicateModal from "@/components/DuplicateModal.vue";
import DeleteModal from "@/components/DeleteModal.vue";
export default {
  name: "Dashboard",
  components: {
    card,
    buttonsuccess,
    DeleteModal,
    DuplicateModal
  },
  data() {
    return {
      showwelcome: true,
      duplicate: { show: false, title: null, index: null },
      del: { show: false, title: null, id: null },
      cardSelection: []
    };
  },
  computed: {
    ...mapGetters(["getCards", "getActivity"])
  },
  methods: {
    ...mapMutations(["setNavState", "duplicateCard", "deleteCard"]),
    openDuplicateModal(e) {
      this.duplicate.show = true;
      this.duplicate.title = e.title;
      this.duplicate.index = e.index;
    },
    duplicateProject() {
      this.duplicate.show = false;
      this.duplicateCard(this.getCards[this.duplicate.index]);
    },
    openDelModal(e) {
      this.del.show = true;
      this.del.title = e.title;
      this.del.id = e.id;
    },
    deleteProject() {
      this.del.show = false;
      this.deleteCard(this.del.id);
    }
  },
  created() {
    this.setNavState("collapse");
    this.cardSelection = this.getCards.slice(0, 3);
  }
};
</script>

<style lang="scss" scoped>
.h-line {
  border-bottom: solid 1px $mercury-40;
}
.v-line {
  border-left: solid 1px $mercury-40;
}
h1 {
  @include Framewrk-Heading-02-Default-28353D;
}
p {
  @include Framewrk-Body-02-Default-28353D;
}
h4 {
  @include Framewrk-Heading-04-Default-28353D;
}
.dash {
  height: 100%;
  background-color: $mercury-20;
}
#header {
  background-color: $mercury-0;
  display: grid;
  grid-auto-rows: 69px 1px;
  .top {
    display: grid;
    grid-template-columns: auto 55px;
    align-items: center;
    padding-left: 24px;
  }
  .bottom {
    display: grid;
    grid-template-columns: auto 40px;
    align-items: center;
    padding-left: 24px;
  }
}
#body {
  .projects {
    .heading {
      display: grid;
      grid-template-columns: auto 150px;
      padding: 24px;
    }
    .cards {
      padding: 20px;
      display: flex;
      justify-content: space-around;
      .card {
        flex: 0 1 24%;
      }
      @media screen and (max-width: 1650px) {
        .card {
          flex: 0 1 32%;
        }
        .card-3 {
          display: none;
        }
      }
      @media screen and (max-width: 1320px) {
        .card {
          flex: 0 1 47%;
        }
        .card-2 {
          display: none;
        }
      }
    }
  }
  .activity {
    padding: 24px;
    .heading {
      padding-bottom: 24px;
    }
    .activity-list {
      .list-item {
        border-radius: 4px;
        box-shadow: 0 1px 2px 0 rgba(0, 0, 0, 0.08);
        border: solid 1px $mercury-40;
        background-color: $mercury-0;
        display: grid;
        grid-template-columns: 150px 1px auto;
        align-items: center;
        padding: 0 16px;
        margin-bottom: 10px;
      }
      p {
        margin-left: 16px;
      }
      .v-line {
        height: 54px;
      }
    }
  }
}
</style>