<template>
  <div class="custom-checkbox" @mouseover="hover=true" @mouseleave="hover = false">
    <div v-if="ticked === false" class="unselected" @click="ticked = true;$emit('input', true)"></div>
    <img
      v-if="ticked === true"
      class="selected"
      @click="ticked = false;$emit('input', false)"
      :src="checktrue"
    />
    <div class="hover-bg" v-if="hover=== true"></div>
  </div>
</template>

<script>
import selectedicon from "@/assets/input/selected-checkbox.svg";
export default {
  props: {
    value: {
      type: Boolean
    }
  },
  data() {
    return {
      ticked: false,
      checktrue: selectedicon,
      hover: false
    };
  },
  created() {
    this.ticked = this.value;
  }
};
</script>

<style lang="scss" scoped>
.custom-checkbox {
  height: 18px;
  width: 18px;
  position: relative;
}
.unselected {
  width: 18px;
  height: 18px;
  border-radius: 2px;
  border: solid 1.5px $outer-space-30;
}
.hover-bg {
  position: absolute;
  top: -10px;
  left: -10px;
  width: 38px;
  height: 38px;
  border-radius: 38px;
  opacity: 0.05;
  background-color: $outer-space-50;
  z-index: -1;
}
</style>