<template>
  <div class="body-container" :class="{'center-flex': $attrs.cards.length === 0}">
    <div class="no-contents" v-if="$attrs.cards.length === 0">No projects currently</div>
    <div class="card-flex">
      <Card
        v-for="(card, i) in $attrs.cards"
        :key="i"
        :index="i"
        :details="card"
        @duplicate="openDuplicateModal"
        @del="openDelModal"
      ></Card>
      <!-- ugly hackz -->
      <div v-if="$attrs.cards.length % 2" class="card tworow"></div>
      <div
        v-if="$attrs.cards.length % 3 === 1 || $attrs.cards.length % 3 === 2"
        class="card threerow"
      ></div>
      <div v-if="$attrs.cards.length % 3 === 1" class="card threerow"></div>

      <div v-if="$attrs.cards.length % 4 === 1" class="card fourrow"></div>
      <div
        v-if="$attrs.cards.length % 4 === 1 || $attrs.cards.length % 4 === 2"
        class="card fourrow"
      ></div>
      <div
        v-if="$attrs.cards.length % 4 === 1 || $attrs.cards.length % 4 === 2 || $attrs.cards.length % 4 === 3"
        class="card fourrow"
      ></div>
    </div>
    <DuplicateModal
      v-if="duplicate.show"
      :cardname="duplicate.title"
      @close="duplicate.show = false"
      @duplicate="duplicateProject"
    ></DuplicateModal>
    <DeleteModal
      v-if="del.show"
      :cardname="del.title"
      @close="del.show = false"
      @delete="deleteProject"
    ></DeleteModal>
  </div>
</template>

<script>
import Card from "@/components/Card.vue";
import DuplicateModal from "@/components/DuplicateModal.vue";
import DeleteModal from "@/components/DeleteModal.vue";
import { mapMutations } from "vuex";
export default {
  components: {
    Card,
    DuplicateModal,
    DeleteModal
  },
  data() {
    return {
      duplicate: { show: false, title: null, index: null },
      del: { show: false, title: null, id: null }
    };
  },
  methods: {
    ...mapMutations(["duplicateCard", "deleteCard"]),
    openDuplicateModal(e) {
      this.duplicate.show = true;
      this.duplicate.title = e.title;
      this.duplicate.index = e.index;
    },
    duplicateProject() {
      this.duplicate.show = false;
      this.duplicateCard(this.$attrs.cards[this.duplicate.index]);
    },
    openDelModal(e) {
      this.del.show = true;
      this.del.title = e.title
      this.del.id = e.id;
    },
    deleteProject() {
      this.del.show = false;
      this.deleteCard(this.del.id);
    }
  }
};
</script> 

<style lang="scss" scoped>
.body-container {
  background-color: $mercury-20;
  padding: 20px;
  .card-flex {
    display: flex;
    flex-wrap: wrap;
    justify-content: space-around;
    .card {
      flex: 0 1 24%;
      margin-bottom: 20px;
    }
    .tworow {
      display: none;
    }
    .threerow {
      display: none;
    }
    .fourrow {
      display: inherit;
    }
    @media screen and (max-width: 1650px) {
      .card {
        flex: 0 1 32%;
      }
      .tworow {
        display: none;
      }
      .threerow {
        display: initial;
      }
      .fourrow {
        display: none;
      }
    }
    @media screen and (max-width: 1320px) {
      .card {
        flex: 0 1 47%;
      }
      .tworow {
        display: initial;
      }
      .threerow {
        display: none;
      }
      .fourrow {
        display: none;
      }
    }
  }
}

.center-flex {
  display: flex;
  align-items: center;
  justify-content: center;
}
.no-contents {
  font-family: Heebo;
  font-size: 20px;
  font-weight: 800;
  font-stretch: normal;
  font-style: normal;
  line-height: 1.3;
  letter-spacing: normal;
  color: $mercury-50;
}
</style>