<template>
  <div>
    <div class="tracking-header">
      <img :src="erroricon" />
      <div class="body-text-2">{{ headertext }}</div>
    </div>
    <div v-for="(snippet, i) in getProjectSettings.tracking.snippets" :key="i">
      <div class="snippet-box">
        <img class="icon" :src="deleteicon" @click="deleteSnippet(i)" />
        <p>Custom Snippet Label</p>
        <input type="text" v-model="snippet.label" @input="changedProject" />
        <p>custom snippet location</p>
        <customselect
          v-model="snippet.location"
          :options="snippet.locationoptions"
          @input="changedProject"
        ></customselect>
        <p>custom snippet code</p>
        <textarea v-model="snippet.code" @input="changedProject"></textarea>
      </div>
    </div>
    <div class="add-new-box" @blur="open = false">
      <div class="add-new-button" :class="{open: open}" @click="open = !open">
        <img :src="addnewicon" />
        <div class="add-new-text">Add New</div>
      </div>
      <div v-if="open" class="menu">
        <div
          class="menu-item"
          v-for="(option, i) in snippetoptions"
          :key="i"
          @click="addSnippet(option); open = false"
        >{{option}}</div>
      </div>
    </div>
  </div>
</template>

<script>
import erroricon from "@/assets/icon/error-stop.svg";
import addnewicon from "@/assets/icon/add-new.svg";
import customselect from "@/components/Input/Select.vue";
import deleteicon from "@/assets/icon/delete.svg";
import { mapGetters, mapMutations } from "vuex";
export default {
  components: {
    customselect
  },
  data() {
    return {
      erroricon: erroricon,
      addnewicon: addnewicon,
      deleteicon: deleteicon,
      open: false,
      snippetoptions: ["facebook", "option 2", "option 3", "option 4"],
      headertext:
        "Warning messages for code, tincidunt lobortis ipsum porttitor sed. Aliquam tempor lobortis felis."
    };
  },
  computed: {
    ...mapGetters(["getProjectSettings"])
  },
  methods: {
    ...mapMutations(["setSettingsChanged"]),
    addSnippet(payload) {
      this.getProjectSettings.tracking.snippets.push({
        for: payload,
        label: "",
        location: "",
        code: "",
        locationoptions: ["Body Groove", "Body End"]
      });
    },
    changedProject() {
      this.setSettingsChanged(true);
    },
    deleteSnippet(payload) {
      this.changedProject()
      this.getProjectSettings.tracking.snippets.splice(payload, 1)
    }
  }
};
</script>

<style lang="scss" scoped>
.tracking-header {
  display: flex;
  align-items: center;
  justify-content: space-around;
  .body-text-2 {
    width: 320px;
  }
}
.snippet-box {
  margin: 30px 0;
  border-radius: 4px;
  border: solid 1px $mercury-30;
  background-color: $mercury-10;
  padding: 17.5px;
  padding-top: 15px;
  position: relative;
  p {
    margin-bottom: 14px;
  }
  .icon {
    position: absolute;
    right: 25px;
    top: 15px;
  }
  input {
    width: 336px !important ;
  }
  textarea {
    width: 336px;
    height: 450px;
    outline: none;
    resize: none;
    border-radius: 4px;
    border: solid 1px $mercury-80;
    font-family: Arial, Helvetica, sans-serif;
    font-size: 13px;
    font-weight: normal;
    font-stretch: normal;
    font-style: normal;
    line-height: 1.31;
    letter-spacing: 0.24px;
    color: $outer-space-50;
    padding: 12px;
  }
}
.add-new-box {
  border-radius: 3px;
  border: solid 1px $mercury-40;
  background-color: $mercury-10;
  padding: 16.5px;
  position: relative;
  .add-new-button {
    border-radius: 4px;
    border: dashed 1px $mercury-50;
    display: flex;
    align-items: center;
    padding: 17px;
    cursor: pointer;
    .add-new-text {
      margin-left: 12px;
      @include Framewrk-Body-01-Subdued-7C868C;
    }
  }
  .open {
    border-color: $sushi-50;
  }
  .menu {
    border-radius: 4px;
    overflow: hidden;
    border: 1px solid $mercury-40;
    position: absolute;
    background-color: $mercury-0;
    top: 80px;
    left: 20px;
    right: 20px;
    .menu-item {
      @include Framewrk-Body-01-Default-28353D;
      margin: 10px 0;
      padding: 5px 0;
      padding-left: 26px;
      &:hover {
        background-color: $mercury-20;
      }
    }
  }
}
</style>