import Vue from 'vue'
import Vuex from 'vuex'

Vue.use(Vuex)

export default new Vuex.Store({
  state: {
    user: {
      username: 'Jack Tricker'
    },
    navigation: 'collapse',
    navigationcurrentpage: 'settings',
    projects: {
      idnum: 1,
      selected: [],
      display: "grid",
      recentactivity: [{
        date: 'today',
        user: 'john'
      }],
      settingsCard: {
        title: 'test'
      },
      settingsChanged: false,
      cards: [{
        id: 0,
        owner: "Claudia Brown",
        created: "2019",
        lastedited: 'Never',
        title: "Jacks Card",
        description: "Jacks Super Card",
        domain: {
          url: 'ft.com',
          urloptions: ['ft.com', 'google.com']
        },
        slug: {
          data: "Data Boi",
          onlyUseRoot: true
        },
        homepage: {
          url: 'Default',
          urloptions: ['Default', 'AOL.com', 'Yahoo.com']
        },
        languages: {
          lang: 'English',
          langoptions: ['English', 'Spanish']
        },
        themes: {
          theme: 'Default',
          themeoptions: ['Default', 'Dark']
        },
        selected: false,
        published: true,
        scheduled: false,
        access: {
          teams: [{
              name: 'Europe',
              selected: true,
              disabled: false
            },
            {
              name: 'Asia',
              selected: false,
              disabled: false
            }
          ],
          users: [{
              name: 'Mum',
              selected: true,
              disabled: false
            },
            {
              name: 'Dad',
              selected: false,
              disabled: false
            },
            {
              name: 'claudia brown',
              selected: false,
              disabled: true
            },
            {
              name: 'finn brown',
              selected: true,
              disabled: true
            }
          ]
        },
        seo: {
          metatitle: 'Blah',
          metadescription: '',
          indexing: false
        },
        tracking: {
          snippets: [{
            for: 'facebook',
            label: "snip",
            location: 'Body Groove',
            locationoptions: ['Body Groove', 'Body End'],
            code: 'beep boop'
          }]
        }
      }]
    }
  },
  getters: {
    getCards(state) {
      return state.projects.cards
    },
    getSelected(state) {
      let numselected = 0
      for (let i = 0; i < state.projects.cards.length; i++) {
        if (state.projects.cards[i].selected === true) {
          numselected += 1
        }
      }
      return numselected
    },
    getProjectSettings(state) {
      return state.projects.settingsCard
    },
    getNavState(state) {
      return state.navigation
    },
    getUser(state) {
      return state.user.username
    },
    getActivity(state) {
      return state.projects.recentactivity
    }
  },
  mutations: {
    addCard(state, payload) {
      let defaultCard = {
        id: null,
        owner: "",
        created: Date()
          .toLocaleString()
          .slice(0, 15),
        lastedited: 'Never',
        title: "",
        description: "",
        domain: {
          url: '',
          urloptions: ['ft.com', 'google.com']
        },
        slug: {
          data: "",
          onlyUseRoot: false
        },
        homepage: {
          url: 'Default',
          urloptions: ['Default', 'AOL.com', 'Yahoo.com']
        },
        languages: {
          lang: 'English',
          langoptions: ['English']
        },
        themes: {
          theme: 'Default',
          themeoptions: ['Default', 'Dark']
        },
        selected: false,
        published: false,
        scheduled: false,
        seo: {
          metatitle: '',
          metadescription: '',
          indexing: false
        },
        tracking: {
          snippets: []
        }
      }

      defaultCard.id = state.projects.idnum
      state.projects.idnum++
      defaultCard.title = payload

      state.projects.cards.push(defaultCard)
    },
    duplicateCard(state, payload) {
      state.projects.idnum++
      let id = state.projects.idnum
      let newCard = JSON.parse(JSON.stringify(payload))
      newCard.title = newCard.title + ' (copy)';
      newCard.id = id
      state.projects.cards.push(newCard)
    },
    deleteCard(state, payload) {
      for (let i = 0; i < state.projects.cards.length; i++) {
        if (state.projects.cards[i].id === payload) {
          state.projects.cards.splice(i, 1)
          console.log(i, payload)
        }
      }
    },
    setProjectDisplay(state, payload) {
      state.projects.display = payload;
    },
    addSelected(state, payload) {
      for (let i = 0; i < state.projects.cards.length; i++) {
        if (state.projects.cards[i].id === payload) {
          state.projects.cards[i].selected = true
        }
      }
    },
    removeSelected(state, payload) {
      for (let i = 0; i < state.projects.cards.length; i++) {
        if (state.projects.cards[i].id === payload) {
          state.projects.cards[i].selected = false
        }
      }
    },
    setSelectedOfAll(state, payload) {
      for (let i = 0; i < state.projects.cards.length; i++) {
        state.projects.cards[i].selected = payload
      }
    },
    setEditedProject(state, payload) {
      state.projects.settingsCard = JSON.parse(JSON.stringify(payload))
    },
    setNavState(state, payload) {
      state.navigation = payload
    },
    setEdits(state, payload) {
      for (let i = 0; i < state.projects.cards.length; i++) {
        if (payload.id === state.projects.cards[i].id) {
          payload.lastedited = Date()
            .toLocaleString()
            .slice(0, 15)
          state.projects.cards[i] = payload
        }
      }
      state.projects.recentactivity.push({
        date: payload.lastedited,
        user: state.user.username
      })
    },
    setSettingsChanged(state, payload) {
      state.projects.settingsChanged = payload
    }
  },
  actions: {},
  modules: {}
})