<template>
  <div class="custom-select" :tabindex="tabindex" @blur="open = false">
    <div class="selected" :class="{open: open}" @click="open = !open">
      <div>{{ selected }}</div>
      <img class="arrow-icon" :class="{'arrow-icon-up': open}" :src="icon.arrow" />
    </div>

    <div class="items" :class="{selectHide: !open}">
      <div
        class="item"
        v-for="(option, i) of options"
        :key="i"
        @click="selected=option; open=false; $emit('input', option)"
      >
        <div>{{ option }}</div>
        <img :src="icon.tick" v-if="selected === option"/>
      </div>
    </div>
  </div>
</template>

<script>
import arrowicon from "@/assets/input/drop-down.svg";
import tickicon from "@/assets/input/light.svg";
export default {
  props: {
    options: {
      type: Array,
      required: true
    },
    tabindex: {
      type: Number,
      required: false,
      default: 0
    },
    value: {
      type: String
    }
  },
  created() {
    this.selected = this.value;
  },
  data() {
    return {
      selected: null,
      open: false,
      icon: { arrow: arrowicon, tick: tickicon }
    };
  }
};
</script>

<style lang="scss" scoped>
* {
  @include Framewrk-Body-01-Default-28353D;
}
.custom-select {
  position: relative;
  width: 100%;
  text-align: left;
  outline: none;
  height: 50px;
  line-height: 47px;
  z-index: 10;
}

.selected {
  background-color: $mercury-0;
  border-radius: 4px;
  border: solid 1px $mercury-80;
  height: 50px;
  padding: 0 16px;
  cursor: pointer;
  user-select: none;
  display: grid;
  align-items: center;
  grid-template-columns: auto 24px;
  .arrow-icon {
    padding: 8px 6px;
    border-radius: 24px;
    transform: rotate(180deg);
    &:hover {
      background-color: $mercury-20;
    }
  }
  .arrow-icon-up {
    transform: rotate(0deg);
  }
}

.selected.open {
  border: solid 1px $sushi-50;
  border-radius: 4px;
}

.selected:after {
  position: absolute;
  content: "";
  top: 22px;
  right: 10px;
  width: 0;
  height: 0;
  border: 4px solid transparent;
  border-color: #fff transparent transparent transparent;
}

.items {
  border-radius: 4px;
  overflow: hidden;
  border: 1px solid $mercury-40;
  position: absolute;
  background-color: $mercury-0;
  top: 60px;
  left: 0;
  right: 0;
}

.item {
  padding: 16px;
  cursor: pointer;
  user-select: none;
  display: grid;
  grid-template-columns: auto 20px;
  align-items: center;
  img {
      z-index: 100;
  }
}

.item:hover {
  background-color: $mercury-20;
}

.selectHide {
  display: none;
}
</style>