<template>
  <div class="container">
    <div class="header">
      <img src="@/assets/logo-full-invert.svg" />
    </div>

    <div class="menu" v-if="getNavState === 'collapse'">
      <div
        class="menu-item"
        :class="{ active: $route.path === item.route }"
        v-for="item in menu"
        v-bind:key="item.id"
        @click="changePage(item.route)"
      >
        <div :class="{ highlight: $route.path === item.route }"></div>
        <div class="menu-icon">
          <img :src="item.activeicon" v-if="$route.path === item.route" />
          <img :src="item.icon" v-else />
        </div>
        <div class="menu-text">
          <p>{{item.name}}</p>
        </div>
      </div>
    </div>

    <div class="menu-expand-container" v-if="getNavState === 'expand'">
      <div class="menu-icons-container">
        <div
          class="menu-item-expand"
          v-for="item in menu"
          v-bind:key="item.id"
          @click="changePage(item.route)"
        >
          <div class="menu-icon">
            <img :src="item.activeicon" v-if="item.route === '/projects'" />
            <img :src="item.icon" v-else />
          </div>
        </div>
      </div>
      <div class="project-expand">
        <h4>{{ $store.state.projects.settingsCard.title }}</h4>
        <p
          :class="{ expandactive: $route.path === '/projects/overview' }"
          @click="changePage('/projects/overview')"
        >Overview</p>
        <p>Activity</p>
        <p>Pages</p>
        <p
          :class="{ expandactive: $route.path === '/projects/settings' }"
          @click="changePage('/projects/settings')"
        >Settings</p>
        <p>Analytics</p>
      </div>
    </div>

    <div class="footer"> 2019 CNK Digital Solutions Ltd</div>
  </div>
</template>

<script>
import homeicon from "@/assets/nav/home-icon.svg";
import projectsicon from "@/assets/nav/projects-icon.svg";
import assetsicon from "@/assets/nav/assets-icon.svg";
import formsicon from "@/assets/nav/forms-icon.svg";
import learningicon from "@/assets/nav/learning-icon.svg";
import homeiconactive from "@/assets/nav/home-active.svg";
import projectsiconactive from "@/assets/nav/projects-active.svg";
import assetsiconactive from "@/assets/nav/assets-active.svg";
import formsiconactive from "@/assets/nav/forms-active.svg";
import learningiconactive from "@/assets/nav/learning-active.svg";
import { mapGetters, mapMutations } from "vuex";
export default {
  name: "NavBar",
  data() {
    return {
      menu: {
        dashboard: {
          id: 1,
          name: "Dashboard",
          dropdown: false,
          route: "/",
          icon: homeicon,
          activeicon: homeiconactive
        },
        projects: {
          id: 2,
          name: "Projects",
          dropdown: false,
          route: "/projects",
          icon: projectsicon,
          activeicon: projectsiconactive
        },
        assets: {
          id: 3,
          name: "Assets",
          dropdown: false,
          route: "/assets",
          icon: assetsicon,
          activeicon: assetsiconactive
        },
        forms: {
          id: 4,
          name: "Forms",
          dropdown: false,
          route: "/forms",
          icon: formsicon,
          activeicon: formsiconactive
        },
        Products: {
          id: 5,
          name: "Learning",
          dropdown: true,
          route: "/learning",
          icon: learningicon,
          activeicon: learningiconactive
        }
      }
    };
  },
  computed: {
    ...mapGetters(["getNavState"])
  },
  methods: {
    ...mapMutations(["setNavState"]),
    changePage(route) {
      this.$router.push(route);
    }
  }
};
</script>

<style lang="scss" scoped>
.container {
  background-color: $outer-space-40;
  color: $mercury-0;
  display: grid;
  grid-template-rows: 70px auto 52px;
  .header {
    height: 70px;
    padding: 21px;
    border-bottom: solid 1px $outer-space-60;
  }
  .menu {
    @include Framewrk-Nav-01-Default-DFDBDB;
    .menu-item {
      height: 56px;
      display: grid;
      grid-template-columns: 4px 66px auto;
      cursor: pointer;
      .highlight {
        background-color: $sushi-50;
        float: left;
      }
      .menu-icon {
        display: flex;
        align-items: center;
        justify-content: center;
        margin-right: 4px;
      }
      .menu-text {
        display: flex;
        align-items: center;
      }
    }
    .active {
      background-color: $outer-space-50;
    }
  }
  .menu-expand-container {
    display: grid;
    grid-template-columns: 70px auto;
    .menu-icon {
      display: flex;
      align-items: center;
      justify-content: center;
      height: 56px;
      cursor: pointer;
    }
    .active {
      background-color: $outer-space-50;
    }
    .project-expand {
      box-shadow: inset 1px 0 9px 0 rgba(0, 0, 0, 0.24);
      background-color: $outer-space-50;
      display: grid;
      grid-template-rows: 60px;
      grid-auto-rows: 40px;
      align-items: center;

      h4 {
        @include Framewrk-Nav-01-Project-Name-FFFFFF;
        margin-left: 25px;
      }
      p {
        @include Framewrk-Body-01-Nav-Level-2-A8AEB1;
        margin-left: 25px;
        padding-left: 17px;
        padding-top: 10px;
        padding-bottom: 10px;
        border-radius: 4px;
        cursor: pointer;
        &:hover {
          background-color: $outer-space-30;
        }
      }
      .expandactive {
        background-color: $outer-space-60;
        &:hover {
          background-color: $outer-space-60;
        }
      }
    }
  }
  .footer {
    height: 52px;
    border-top: solid 1px $outer-space-60;

    padding-top: 16px;
    padding-bottom: 16px;
    padding-left: 22px;

    font-family: Heebo;
    font-size: 11px;
    font-weight: normal;
    font-stretch: normal;
    font-style: normal;
    line-height: 1.82;
    letter-spacing: normal;
    color: $mercury-80;
  }
}
</style>