<template>
  <div class="container">
    <ProjectBar @newprojectbutton="newproject"></ProjectBar>
    <ProjectBodyGrid v-if="$store.state.projects.display === 'grid'" :cards="getCards"></ProjectBodyGrid>
    <ProjectBodyList v-if="$store.state.projects.display === 'list'" :cards="getCards"></ProjectBodyList>
  </div>
</template>

<script>
import ProjectBar from "@/components/ProjectBar.vue";
import ProjectBodyGrid from "@/components/ProjectBodyGrid.vue";
import ProjectBodyList from "@/components/ProjectBodyList.vue";
import { mapMutations, mapGetters } from "vuex";
export default {
  name: "projects",
  components: {
    ProjectBar,
    ProjectBodyGrid,
    ProjectBodyList
  },
  computed: {
    ...mapGetters(["getCards"])
  },

  methods: {
    ...mapMutations(["addCard", "setNavState"]),
    newproject(payload) {
      this.addCard(payload);
    }
  },
  created() {
    this.setNavState("collapse");
  }
};
</script>

<style lang="scss" scoped>
.container {
  height: 100%;
  display: grid;
  grid-template-rows: 70px auto;
}
</style>