<template>
  <div class="box">
    <input
      class="search-input"
      type="text"
      v-model="search"
      :placeholder="'Search ' + placeholdertext"
    />
    <img class="icon" :src="searchicon" />
    <div class="search-content">
      <div class="option" v-for="(option, i) in data" :key="i">
        <div class="option-line" v-if="matchSearch(option)">
          <customcheck
            v-model="option.selected"
            @input="$emit('changed')"
            v-if="option.disabled === false"
          ></customcheck>
          <div
            v-if="option.disabled === true && option.selected === true"
            class="disabled-checkbox-true"
          ></div>
          <div
            v-if="option.disabled === true && option.selected === false"
            class="disabled-checkbox-false"
          ></div>
          {{option.name}}
        </div>
      </div>
    </div>
  </div>
</template>

<script>
import customcheck from "@/components/Input/Checkbox.vue";
import searchicon from "@/assets/icon/search.svg";
export default {
  components: {
    customcheck
  },
  props: {
    value: {
      type: Array
    },
    placeholdertext: {
      type: String
    }
  },
  data() {
    return {
      search: "",
      data: null,
      searchicon
    };
  },
  created() {
    this.data = this.value;
  },
  methods: {
    matchSearch(e) {
      return this.filteredOptionObj.includes(e);
    }
  },
  computed: {
    filteredOptionObj() {
      return this.data.filter(e =>
        e.name.toLowerCase().includes(this.search.toLowerCase())
      );
    }
  }
};
</script>

<style lang="scss" scoped>
.box {
  position: relative;
  .search-input {
    border-radius: 4px 4px 0 0;
    border-bottom: none;
    padding-left: 60px;
  }
  .icon {
    position: absolute;
    left: 16px;
    top: 14px;
  }
  .search-content {
    width: 373px;
    height: 300px;
    overflow-y: auto;
    border-radius: 0 0 4px 4px;
    border: solid 1px $mercury-80;
    .option {
      padding-top: 6px;
      .option-line {
        padding: 10px 16px;
        display: grid;
        grid-template-columns: 35px auto;

        @include Framewrk-Body-01-Default-28353D;
        .disabled-checkbox-true {
          width: 18px;
          height: 18px;
          border-radius: 2px;
          background-color: $mercury-50;
        }
        .disabled-checkbox-false {
          width: 18px;
          height: 18px;
          border-radius: 2px;
          border: solid 1.5px $mercury-50;
        }
      }
    }
  }
}
</style>