<template>
  <div class="modal-bg">
    <div class="modal-box">
      <h3>Create a new project</h3>
      <div class="label">PROJECT NAME</div>
      <input v-model="cardName" type="text" @keyup.enter="createCard" ref="cardNameInput" />
      <div class="modal-line"></div>
      <div class="button-container">
        <ButtonDefault @clicked="closeModal">Cancel</ButtonDefault>
        <div class="gap"></div>
        <ButtonSuccess @clicked="createCard">Create</ButtonSuccess>
      </div>
    </div>
  </div>
</template>

<script>
import ButtonSuccess from "@/components/ButtonSuccess.vue";
import ButtonDefault from "@/components/ButtonDefault.vue";
export default {
  components: {
    ButtonSuccess,
    ButtonDefault
  },
  data() {
    return {
      cardName: ""
    };
  },
  mounted() {
    this.$refs.cardNameInput.focus();
  },
  methods: {
    closeModal() {
      this.$emit("close");
    },
    createCard() {
      if (!this.cardName) {
        alert("cannot be empty name");
      } else {
        this.$emit("newcard", this.cardName);
      }
    }
  }
};
</script>

<style lang="scss" scoped>
.modal-bg {
  position: fixed;
  z-index: 10;
  left: 0;
  top: 0;
  width: 100%;
  height: 100%;
  background-color: rgba($color: #000000, $alpha: 0.55);
  display: flex;
  align-items: center;
  justify-content: center;

  .modal-box {
    width: 456px;
    height: 244px;
    border-radius: 4px;
    box-shadow: 2px 10px 18px 6px rgba(0, 0, 0, 0.2);
    background-color: $mercury-0;
    padding: 18px 21px;
    h3 {
      font-family: Heebo;
      font-size: 18px;
      font-weight: 500;
      font-stretch: normal;
      font-style: normal;
      line-height: 1.11;
      letter-spacing: normal;
      color: $outer-space-50;
      margin-bottom: 18px;
    }
    .label {
      font-family: Heebo;
      font-size: 11.5px;
      font-weight: normal;
      font-stretch: normal;
      font-style: normal;
      line-height: 1.48;
      letter-spacing: 0.64px;
      color: $outer-space-30;
      margin-bottom: 11px;
      margin-left: 3px;
    }
    input {
      border-radius: 4px;
      border: solid 1px $mercury-80;
      width: 100%;
      height: 50px;
      padding-left: 16px;
      font-family: Heebo;
      font-size: 13px;
      font-weight: normal;
      font-stretch: normal;
      font-style: normal;
      line-height: normal;
      letter-spacing: 0.28px;
      color: $outer-space-50;
      margin-bottom: 34px;
    }
    .modal-line {
      width: 456px;
      height: 0;
      border-top: solid 1px $mercury-40;
      margin-left: -21px;
      margin-bottom: 19px;
    }
    .button-container {
      display: flex;
      justify-content: flex-end;
      .gap {
        width: 12px;
      }
    }
  }
}
</style>