<template>
  <div class="card">
    <div class="image" v-if="!show.action">
      <img src="https://picsum.photos/300/100" />
    </div>

    <div class="name">
      <div class="oval" :class="{ 'published': details.published }"></div>
      <h4>{{details.title}}</h4>
    </div>

    <div class="h-line" v-if="show.action === true"></div>

    <div class="actions" v-if="show.action === true">
      <div class="action-links">
        <img :src="settingsicon" @click="settingsPage" />
        <h6 @click="settingsPage">Settings</h6>
      </div>
      <div class="action-links">
        <img @click="$emit('duplicate', {index: index, title: details.title})" :src="duplicateicon" />
        <h6 @click="$emit('duplicate', {index: index, title: details.title})">Duplicate</h6>
      </div>
      <div class="action-links">
        <img :src="editicon" />
        <h6>Edit</h6>
      </div>
      <div class="action-links">
        <img @click="$emit('del', {title: details.title, id: details.id})" :src="deleteicon" />
        <h6 @click="$emit('del', {title: details.title, id: details.id})">Delete</h6>
      </div>
    </div>

    <div class="h-line"></div>

    <div class="footer">
      <div class="dates">
        <div class="label">Date Created</div>
        <div class="label">Last Edited</div>
        <p>{{details.created}}</p>
        <p>{{details.lastedited}}</p>
      </div>

      <div class="v-line"></div>
      <div class="hamburger" @click="expandCard">
        <img :src="expandicon" />
      </div>
    </div>
  </div>
</template>

<script>
import hamburgericon from "@/assets/hamburger-menu.svg";
import deleteicon from "@/assets/icon/delete.svg";
import editicon from "@/assets/icon/edit.svg";
import duplicateicon from "@/assets/icon/duplicate.svg";
import settingsicon from "@/assets/icon/settings.svg";
import crossicon from "@/assets/icon/cross.svg";
import { mapMutations } from "vuex";
export default {
  props: {
    details: {
      type: Object
    },
    index: {
      type: Number
    }
  },
  data() {
    return {
      show: {
        duplicate: false,
        delete: false,
        action: false
      },
      expandicon: hamburgericon,
      deleteicon: deleteicon,
      editicon: editicon,
      duplicateicon: duplicateicon,
      settingsicon: settingsicon,
      crossicon: crossicon
    };
  },
  methods: {
    ...mapMutations(["setNavState", "setEditedProject"]),
    settingsPage() {
      this.setNavState("expand");
      this.setEditedProject(this.details);
      this.$router.push("/projects/settings");
    },
    expandCard() {
      if (this.show.action === false) {
        this.expandicon = crossicon;
      } else {
        this.expandicon = hamburgericon;
      }
      this.show.action = !this.show.action;
    }
  }
};
</script>

<style lang="scss" scoped>
.card {
  height: 223px;
  object-fit: contain;
  border-radius: 7px;
  box-shadow: 0 1px 2px 0 rgba(0, 0, 0, 0.08);
  border: solid 1px $mercury-40;
  background-color: $mercury-0;
  contain: content;

  display: flex;
  flex-direction: column;
  justify-content: flex-end;
  overflow: hidden;
  .image {
    img {
      width: 500px;
    }
    height: 55%;
    overflow: hidden;
  }
  .actions {
    height: 50%;
    display: grid;
    grid-template-columns: 1fr 1fr;
    align-items: center;
    h6 {
      font-family: Heebo;
      font-size: 14px;
      font-weight: normal;
      font-stretch: normal;
      font-style: normal;
      line-height: normal;
      letter-spacing: 0.3px;
      color: $mercury-80;
      margin: 0;
      cursor: pointer;
    }
    img {
      margin-right: 12px;
      cursor: pointer;
    }
    .action-links {
      display: flex;
      padding-left: 20px;
    }
  }
  .name {
    height: 50px;
    display: flex;
    align-items: center;
    padding-left: 20px;
    h4 {
      font-family: Heebo;
      font-size: 14px;
      font-weight: 500;
      font-stretch: normal;
      font-style: normal;
      line-height: normal;
      letter-spacing: normal;
      color: $outer-space-50;
    }
    .oval {
      width: 14px;
      height: 14px;
      border-radius: 14px;
      border: solid 1px $mercury-30;
      background-color: $unpublished-50;
      margin-right: 12px;
    }
    .published {
      background-color: $sushi-50;
    }
  }
  .h-line {
    border-top: solid 1px $mercury-40;
  }
  .footer {
    height: 55px;
    display: grid;
    align-items: center;
    grid-template-columns: auto 1px 66px;
    .dates {
      display: grid;
      grid-template-columns: 2fr 2fr;
      grid-template-rows: 1fr 1fr;
      align-items: center;
      p {
        font-family: Heebo;
        font-size: 13px;
        font-weight: normal;
        font-stretch: normal;
        font-style: normal;
        line-height: normal;
        letter-spacing: 0.28px;
        color: $outer-space-20;
        padding-left: 20px;
      }
      .label {
        font-family: Heebo;
        font-size: 10px;
        font-weight: normal;
        font-stretch: normal;
        font-style: normal;
        line-height: normal;
        letter-spacing: 0.36px;
        color: $mercury-80;
        text-transform: uppercase;
        padding-left: 20px;
      }
    }
    .v-line {
      border-left: solid 1px $mercury-40;
      height: 55px;
    }
    .hamburger {
      display: flex;
      align-items: center;
      justify-content: center;
      cursor: pointer;
    }
  }
}
</style>