<template>
  <div>
    <div class="nav">
      <h2>Project Settings</h2>
      <div class="h-line"></div>
      <div class="tabs">
        <div class="tabs-container">
          <h4 @click="navigate('basic')" :class="{ underline : show.body === 'basic' }">Basic</h4>
          <h4 @click="navigate('access')" :class="{ underline : show.body === 'access' }">Access</h4>
          <h4 @click="navigate('seo')" :class="{ underline : show.body === 'seo' }">SEO Social</h4>
          <h4
            @click="navigate('tracking')"
            :class="{ underline : show.body === 'tracking' }"
          >Tracking Analytics</h4>
        </div>
      </div>
      <div class="h-line"></div>
    </div>
    <div class="body">
      <div class="body-container">
        <component :is="show.body"></component>
      </div>
      <div class="footer-ps">
        <ButtonDefault @clicked="cancelButton" class="error-button">Cancel</ButtonDefault>
        <div class="gap"></div>
        <ButtonSuccess
          :class="{ disabled: !$store.state.projects.settingsChanged }"
          @clicked="saveEdits"
        >Save</ButtonSuccess>
      </div>
    </div>
    <div class="modal-bg" v-if="show.unsaved || show.manditory">
      <div class="modal-box">
        <div class="unsaved-changes" v-if="show.unsaved">
          <h3>You have unsaved changes</h3>
          <div class="body-text-2">Would you like to save changes to this project?</div>
          <div class="h-line"></div>
          <div class="button-container">
            <div class="left">
              <ButtonDefault @clicked="dontSaveLeave()">Don't Save</ButtonDefault>
            </div>
            <div class="right">
              <ButtonDefault @clicked="show.unsaved = false">Cancel</ButtonDefault>
              <div class="gap"></div>
              <ButtonSuccess @clicked="saveEditsLeave()">Save Exit</ButtonSuccess>
            </div>
          </div>
        </div>
        <div class="manditory-changes" v-if="show.manditory">
          <h3>Warning! Incomplete mandatory fields</h3>
          <div
            class="body-text-2"
          >You have incomplete mandatory fields, continue editing to address these, or press exit to leave and delete this page.</div>
          <div class="h-line"></div>
          <div class="button-container">
            <ButtonDefault @clicked="show.manditory = false">Close</ButtonDefault>
          </div>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
import basic from "@/components/ProjectSettings/Basic.vue";
import seo from "@/components/ProjectSettings/SEO.vue";
import access from "@/components/ProjectSettings/Access.vue";
import tracking from "@/components/ProjectSettings/Tracking.vue";
import ButtonSuccess from "@/components/ButtonSuccess.vue";
import ButtonDefault from "@/components/ButtonDefault.vue";
import { mapMutations, mapGetters } from "vuex";
export default {
  name: "ProjectSettings",
  components: {
    basic,
    seo,
    access,
    tracking,
    ButtonSuccess,
    ButtonDefault
  },
  computed: {
    ...mapGetters(["getProjectSettings"])
  },
  data() {
    return {
      show: { body: "basic", unsaved: false, manditory: false },
      backup: null,
      unsaved: { routeto: null }
    };
  },
  created() {
    this.setNavState("expand");
  },
  mounted() {
    this.backup = { ...this.getProjectSettings };
  },
  methods: {
    ...mapMutations([
      "setEdits",
      "setNavState",
      "setEditedProject",
      "setSettingsChanged"
    ]),
    navigate(target) {
      this.show.body = target;
    },
    saveEdits() {
      if (this.checkManditoryFields()) {
        this.show.manditory = true;
      } else {
        this.setEdits(this.$store.state.projects.settingsCard);
        this.setSettingsChanged(false);
      }
    },
    saveEditsLeave() {
      this.setEdits(this.$store.state.projects.settingsCard);
      this.setSettingsChanged(false);
      this.$router.push(this.unsaved.routeto);
    },
    dontSaveLeave() {
      this.setSettingsChanged(false);
      this.$router.push(this.unsaved.routeto);
    },
    cancelButton() {
      this.setEditedProject(this.backup);
      this.setSettingsChanged(false);
      // this.setNavState("expand");
    },
    checkManditoryFields() {
      let e = this.getProjectSettings;
      if (!e.title) {
        return true;
      }
    }
  },
  beforeRouteLeave(to, from, next) {
    if (this.checkManditoryFields()) {
      this.show.manditory = true;
    } else if (this.$store.state.projects.settingsChanged) {
      this.unsaved.routeto = to.path;
      this.show.unsaved = true;
    } else {
      next(true);
    }
  }
};
</script>

<style lang="scss">
.nav {
  display: grid;
  grid-template-rows: 70px 1px 50px 1px auto;
  align-items: center;
  h2 {
    @include Framewrk-Heading-02-Default-28353D;
    margin-left: 23px;
  }
  .h-line {
    border-bottom: solid 1px $mercury-40;
  }
  .tabs {
    display: flex;
    justify-content: center;
    align-items: center;
    .tabs-container {
      width: 400px;
      display: flex;
      justify-content: space-around;
      align-items: center;
      h4 {
        @include Framewrk-Heading-04-Subdued-7C868C;
        cursor: pointer;
      }
      .underline {
        border-bottom: solid 3px $sushi-50;
        @include Framewrk-Heading-04-Default-28353D;
      }
    }
  }
}
.body {
  display: flex;
  flex-direction: column;
  align-items: center;
  .body-container {
    width: 373px;
    p {
      margin-left: 3px;
      @include Framewrk-LABEL-01-DEFAULT-28353D;
      text-transform: uppercase;
      span {
        @include Framewrk-LABEL-01-Optional-7C868C;
        text-transform: capitalize;
      }
    }
    input {
      width: 373px;
      height: 50px;
      border-radius: 4px;
      border: solid 1px $mercury-80;
      padding-left: 16px;
      @include Framewrk-Body-01-Default-28353D;
      outline: none;
      box-sizing: border-box;
      &:focus {
        border-color: $sushi-50;
      }
    }
    .body-text-1 {
      @include Framewrk-Body-01-Default-28353D;
    }
    .body-text-2 {
      @include Framewrk-Body-02-Default-28353D;
    }
    .h-line {
      border-bottom: solid 1px $mercury-40;
    }
  }
}
.footer-ps {
  margin: 50px 0px;
  display: flex;
  width: 373px;
  justify-content: flex-end;
  .gap {
    width: 10px;
  }
  .error-button {
    color: $error-50;
    border-color: $error-50;
  }
  .disabled {
    background-color: $mercury-30;
    color: $mercury-50;
    cursor: default;
  }
}
.modal-bg {
  position: fixed;
  z-index: 10;
  left: 0;
  top: 0;
  width: 100%;
  height: 100%;
  background-color: rgba($color: #000000, $alpha: 0.55);
  display: flex;
  align-items: center;
  justify-content: center;
  .modal-box {
    width: 456px;
    border-radius: 4px;
    box-shadow: 2px 10px 18px 6px rgba(0, 0, 0, 0.2);
    background-color: $mercury-0;
    padding: 18px 21px;
    .unsaved-changes {
      h3 {
        @include Framewrk-Heading-03-Default-28353D;
        margin-bottom: 18px;
      }
      .body-text-2 {
        @include Framewrk-Body-02-Default-28353D;
        margin-bottom: 29px;
      }
      .h-line {
        border-bottom: solid 1px $mercury-40;
        margin-left: -21px;
        margin-right: -21px;
      }
      .button-container {
        margin-top: 19px;
        display: grid;
        grid-template-columns: auto auto;
        .left {
          display: flex;
        }
        .right {
          display: flex;
          justify-content: flex-end;
          .gap {
            width: 10px;
          }
        }
      }
    }
    .manditory-changes {
      h3 {
        @include Framewrk-Heading-03-Default-28353D;
        margin-bottom: 18px;
      }
      .body-text-2 {
        @include Framewrk-Body-02-Default-28353D;
        margin-bottom: 29px;
      }
      .h-line {
        border-bottom: solid 1px $mercury-40;
        margin-left: -21px;
        margin-right: -21px;
      }
      .button-container {
        margin-top: 19px;
        display: flex;
        justify-content: flex-end;
      }
    }
  }
}
</style>