<template>
  <div id="basicbody">
    <div class="name">
      <p>Name</p>
      <input type="text" v-model="getProjectSettings.title" @input="changedProject" />
    </div>
    <div class="description">
      <p>
        Description
        <span>(optional)</span>
      </p>
      <textarea
        name="description"
        id="description"
        cols="50"
        rows="4"
        v-model="getProjectSettings.description"
        @input="changedProject"
      ></textarea>
    </div>
    <div class="domain">
      <p>Domain</p>
      <customselect
        class="vue-select"
        @input="changedProject"
        :options="getProjectSettings.domain.urloptions"
        v-model="getProjectSettings.domain.url"
      ></customselect>
    </div>
    <div class="slug">
      <p :class="{transparent: getProjectSettings.slug.onlyUseRoot}">Slug</p>
      <input
        type="text"
        v-model="getProjectSettings.slug.data"
        :disabled="getProjectSettings.slug.onlyUseRoot"
        @input="changedProject"
        :class="{transparent: getProjectSettings.slug.onlyUseRoot}"
      />
      <div class="slug-checkbox">
        <div class="body-text-2">Only use root domain</div>
        <customcheckbox
          type="checkbox"
          v-model="getProjectSettings.slug.onlyUseRoot"
          @input="changedProject"
        ></customcheckbox>
      </div>
    </div>
    <div class="favicon">
      <p>
        Favicon
        <span>(optional)</span>
      </p>
      <div
        class="body-text-2"
      >Using the field below, you can upload a browser URL icon for use with your page. Your favicon can be 16 x 16 or 32 x 32 px in size, in the file format .png, .ico or .bmp.</div>
      <div class="file-drop-box"></div>
    </div>
    <div class="url-preview">
      <p>URL Preview</p>
      <div class="url-box">
        <div class="body-text-1">{{getProjectSettings.domain.url}}</div>
      </div>
    </div>
    <div class="homepage">
      <p>homepage</p>
      <customselect
        class="vue-select"
        v-model="getProjectSettings.homepage.url"
        @input="changedProject"
        :options="getProjectSettings.homepage.urloptions"
      ></customselect>
    </div>
    <div class="languages">
      <p>languages</p>
      <div class="languages-box">
        <p>Default</p>
        <customselect
          class="vue-select"
          v-model="getProjectSettings.languages.lang"
          @input="changedProject"
          :options="getProjectSettings.languages.langoptions"
        ></customselect>
        <div class="h-line"></div>
        <p>Additional</p>
        <div class="add-new">
          <div class="add-new-text">Add New</div>
        </div>
      </div>
    </div>
    <div class="theme">
      <p>theme</p>
      <customselect
        v-model="getProjectSettings.themes.theme"
        @input="changedProject"
        :options="getProjectSettings.themes.themeoptions"
      ></customselect>
    </div>
    <div class="status">
      <p>status</p>
      <div class="published-indicator">
        <div class="oval" :class="{ 'oval-green': getProjectSettings.published}"></div>
        <div class="body-text-1" v-if="getProjectSettings.published">Published</div>
        <div class="body-text-1" v-if="!getProjectSettings.published">Unpublished</div>
      </div>
      <div class="published-select">
        <div class="spacing">
          <input
            type="radio"
            id="unpublished"
            :value="false"
            name="publish-radio"
            v-model="getProjectSettings.published"
            @input="changedProject"
          />
          <label for="unpublished">Unpublished</label>
        </div>
        <div class="h-line"></div>
        <div class="spacing">
          <input
            type="radio"
            id="published"
            :value="true"
            name="publish-radio"
            v-model="getProjectSettings.published"
            @input="changedProject"
          />
          <label for="published">Published</label>
        </div>
      </div>
    </div>

    <div class="schedule">
      <div class="h-line"></div>
      <div class="schedule-line">
        <div class="body-text-1">Schedule</div>
        <customcheckbox
          type="checkbox"
          v-model="getProjectSettings.scheduled"
          @input="changedProject"
        ></customcheckbox>
      </div>
      <div class="h-line"></div>
    </div>
  </div>
</template>

<script>
import { mapGetters, mapMutations } from "vuex";
import customselect from "@/components/Input/Select.vue";
import customcheckbox from "@/components/Input/Checkbox.vue";
export default {
  name: "basic",
  components: { customcheckbox, customselect },
  computed: {
    ...mapGetters(["getProjectSettings"])
  },
  data() {
    return {
      project: null,
      changed: false
    };
  },
  created() {
    this.project = { ...this.getProjectSettings };
  },
  methods: {
    ...mapMutations(["setSettingsChanged"]),
    changedProject() {
      this.setSettingsChanged(true);
    }
  }
};
</script>

<style lang="scss" scoped>
#basicbody {
  padding-top: 30px;
}
p {
  margin-bottom: 9px;
}
.description {
  margin-top: 25px;
  textarea {
    width: 373px;
    resize: none;
    border-radius: 4px;
    border: solid 1px $outer-space-50;
    outline: none;
    padding: 16px;
    @include Framewrk-Body-01-Default-28353D;
    margin: 0;
    &:focus {
      border-color: $sushi-50;
    }
  }
}
.domain {
  margin-top: 20px;
}
.slug {
  margin-top: 20px;
  .transparent {
    opacity: 0.4;
  }
  .slug-checkbox {
    margin-top: 14px;
    display: grid;
    grid-template-columns: auto 20px;
    align-items: center;
    .body-text-2 {
      margin-left: 3px;
    }
    input {
      margin: 0;
      padding: 0;
      width: 20px;
      height: 15px;
    }
  }
}
.favicon {
  margin-top: 25px;
  .body-text-2 {
    margin: 0 4px;
    margin-bottom: 19px;
  }
  .file-drop-box {
    width: 373px;
    height: 180px;
    border-radius: 4px;
    border: dashed 1px $mercury-50;
    background-color: $mercury-10;
  }
}
.url-preview {
  margin-top: 25px;
  .url-box {
    width: 373px;
    height: 50px;
    border-radius: 4px;
    background-color: $mercury-20;
    padding: 16px;
  }
}
.homepage {
  margin-top: 25px;
  .vue-select {
    z-index: 11;
  }
}
.languages {
  margin-top: 25px;
  .languages-box {
    width: 373px;
    height: 217px;
    border-radius: 4px;
    border: solid 1px $mercury-40;
    background-color: $mercury-10;
    padding: 13px 18px;
    .h-line {
      margin-top: 18px;
      margin-bottom: 14px;
    }
    .add-new {
      width: 309px;
      height: 50px;
      border-radius: 4px;
      border: dashed 1px $mercury-50;
      padding: 16px;
      .add-new-text {
        @include Framewrk-Body-01-Subdued-7C868C;
      }
    }
  }
}
.theme {
  margin-top: 25px;
  select {
    width: 373px;
    height: 50px;
    border-radius: 4px;
    border: solid 1px $outer-space-50;
    padding-left: 16px;
    @include Framewrk-Body-01-Default-28353D;
    outline: none;
    box-sizing: border-box;
    background-color: $mercury-0;
  }
}
.status {
  margin-top: 25px;
  .published-indicator {
    display: flex;
    margin-bottom: 20px;
    .oval {
      width: 16px;
      height: 16px;
      border: solid 1px $mercury-30;
      border-radius: 16px;
      background-color: $unpublished-50;
      margin-left: 13px;
      margin-right: 11px;
    }
    .oval-green {
      background-color: $sushi-50;
    }
  }
  .published-select {
    width: 373px;
    height: 81px;
    border-radius: 4px;
    border: solid 1px $mercury-60;
    @include Framewrk-Body-01-Default-28353D;
    input {
      width: 20px;
      height: 20px;
    }
    .spacing {
      height: 40px;
      display: flex;
      align-items: center;
      padding-left: 16px;
    }
  }
}
.schedule {
  margin-top: 25px;
  .schedule-line {
    display: grid;
    grid-template-columns: auto 18px;
    grid-template-rows: 18px;
    align-items: center;
    margin: 20px 0;
    input {
      height: 20px;
      width: 20px;
    }
    .body-text-1 {
      margin-left: 3px;
    }
  }
}
</style>