<template>
  <div class="list-item-container">
    <div class="checkbox-container">
      <customcheckbox v-model="selected" @input="emitvalue"></customcheckbox>
    </div>
    <div class="list-item-box" :class=" {expanded: expandeditem}">
      <div class="center-flex">
        <div class="oval" :class="{'oval-green': details.published}"></div>
      </div>
      <div class="dividing-line"></div>
      <div class="item-details">
        <h4>{{ details.title }}</h4>
      </div>
      <p>{{ details.created }}</p>

      <p>{{ details.lastedited }}</p>

      <p>{{ details.owner }}</p>

      <div class="dividing-line"></div>
      <div class="center-flex">
        <img style="cursor:pointer" :src="expandicon" @click="toggleHamburger" />
      </div>
      <div v-if="expandeditem" class="h-line"></div>
      <div v-if="expandeditem" class="expanded-container">
        <img :src="settingsicon" @click="settingsPage" />
        <div class="settings pointer" @click="settingsPage">Settings</div>

        <img @click="show.duplicate = true" :src="duplicateicon" />
        <div class="duplicate pointer" @click="show.duplicate = true">Duplicate</div>

        <DuplicateModal
          v-if="show.duplicate"
          :cardname="details.title"
          @close="show.duplicate = false"
          @duplicate="duplicateCard(details);show.duplicate = false"
        ></DuplicateModal>

        <img :src="editicon" />
        <div class="editor pointer">Editor</div>

        <img @click="show.delete = true" :src="deleteicon" />
        <div class="delete pointer" @click="show.delete = true">Delete</div>

        <DeleteModal
          v-if="show.delete"
          :cardname="details.title"
          @close="show.delete = false"
          @delete="deleteItem"
        ></DeleteModal>
      </div>
    </div>
  </div>
</template>

<script>
import hamburgericon from "@/assets/hamburger-menu.svg";
import deleteicon from "@/assets/icon/delete.svg";
import editicon from "@/assets/icon/edit.svg";
import duplicateicon from "@/assets/icon/duplicate.svg";
import settingsicon from "@/assets/icon/settings.svg";
import crossicon from "@/assets/icon/cross.svg";
import { mapMutations } from "vuex";
import DuplicateModal from "@/components/DuplicateModal.vue";
import DeleteModal from "@/components/DeleteModal.vue";
import customcheckbox from "@/components/Input/Checkbox.vue";
export default {
  components: {
    DuplicateModal,
    DeleteModal,
    customcheckbox
  },
  data() {
    return {
      expandicon: hamburgericon,
      hamburgericon: hamburgericon,
      deleteicon: deleteicon,
      editicon: editicon,
      duplicateicon: duplicateicon,
      settingsicon: settingsicon,
      crossicon: crossicon,
      selected: false,
      expandeditem: false,
      show: { duplicate: false, delete: false }
    };
  },
  props: {
    details: {
      type: Object
    }
  },
  methods: {
    ...mapMutations([
      "addSelected",
      "removeSelected",
      "deleteCard",
      "setNavState",
      "setEditedProject",
      "duplicateCard"
    ]),
    addToSelected() {
      if (!this.selected) {
        this.addSelected(this.details.id);
      } else {
        this.removeSelected(this.details.id);
      }
    },
    deleteItem() {
      this.deleteCard(this.details.id);
      this.show.delete = false;
    },
    toggleHamburger() {
      this.expandeditem = !this.expandeditem;
      if (this.expandeditem === true) {
        this.expandicon = this.crossicon;
      } else {
        this.expandicon = this.hamburgericon;
      }
    },
    settingsPage() {
      this.setNavState("expand");
      this.setEditedProject(this.details);
      this.$router.push("/projects/settings");
    },
    emitvalue(e) {
      this.$emit('input', e)
    }
  }
};
</script>

<style lang="scss" scoped>
img {
  cursor: pointer;
}
.list-item-container {
  display: grid;
  grid-template-columns: 34px auto;

  .checkbox-container {
    display: flex;
    align-items: center;
  }
  .list-item-box {
    object-fit: contain;
    border-radius: 4px;
    box-shadow: 0 1px 2px 0 rgba(0, 0, 0, 0.08);
    border: solid 1px $mercury-40;
    background-color: $mercury-0;
    display: grid;
    grid-template-columns: 69px 1px auto 130px 130px 190px 1px 69px;
    grid-template-rows: 53px;
    align-items: center;
    .center-flex {
      display: flex;
      align-items: center;
      justify-content: center;
    }
    .oval {
      width: 14px;
      height: 14px;
      border: solid 1px $mercury-30;
      border-radius: 14px;
      background-color: $unpublished-50;
    }
    .oval-green {
      background-color: $sushi-50;
    }
    .dividing-line {
      height: 54px;
      border-left: solid 1px $mercury-40;
    }
    .item-details {
      display: flex;
      align-items: center;
      margin-left: 16px;
    }
    h4 {
      @include Framewrk-Heading-04-Default-28353D;
    }
    p {
      @include Framewrk-Body-02-Default-28353D;
    }
    .h-line {
      border-top: solid 1px $mercury-40;
      width: 100%;
      grid-row-start: 2;
      grid-row-end: 3;
      grid-column-start: 1;
      grid-column-end: 9;
    }
    .expanded-container {
      padding-left: 20px;
      grid-row-start: 3;
      grid-row-end: 4;
      grid-column-start: 1;
      grid-column-end: 9;
      display: grid;
      grid-template-columns: repeat(2, 30px 80px) 30px 60px 30px 80px;
      align-items: center;

      font-family: Heebo;
      font-size: 14px;
      font-weight: normal;
      font-stretch: normal;
      font-style: normal;
      line-height: normal;
      letter-spacing: 0.3px;
      color: $mercury-80;
      .pointer {
        cursor: pointer;
      }
    }
  }
  .expanded {
    grid-template-rows: 53px 1px 53px;
  }
}
</style>